import { makeStyles } from '@material-ui/core/styles';

export default makeStyles({
    media:{
        height: 250,
        
    },
    border: {
        border: 'solid',
      },
      fullHeightCard: {
        height: '100%',
      },
      card: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        backgroundColor:'#20BCFF',
      },
      activeCard: {
        borderBottom: '10px solid #22289a',
      },
      grid: {
        display: 'flex',
      },
      details: {
        display: 'flex',
        justifyContent: 'space-between',
        margin: '20px',
       
      },
      title: {
        padding: '0 16px',
      },
      cardActions: {
        padding: '8px 16px 8px 16px',
        display: 'flex',
        justifyContent: 'space-between',
        backgroundColor:'#000000',
        color:'#fff',
      },
      
      

});